<template>
  <div class="build1">
    <back-home></back-home>
    <transition name="part1">
      <div class="part1" v-if="part1Show" @mousewheel.prevent="mouseWheel">
        <div class="banner" ref="banner1" :style="{ left: imgLeft + 'px' }">
          <img
            src="../../../assets/buildInfo/build2/米氏力奇-06.png"
            @load="imgLoad"
          />
        </div>
        <div class="word" ref="word1" :style="{ top: wordTop + 'px' }">
          <p class="pFirst">隐匿在小巷子中的</p>
          <p>「米氏力奇研究所」到</p>
          <p>一栋三层宽敞开阔着的「米氏力奇有限公司」,</p>
          <p>这是一家飘着阵阵黄油香气和人情味儿浓郁的 面包店。</p>
        </div>
      </div>
    </transition>
    <transition name="part2">
      <div class="part2 part" v-if="part2Show">
        <div class="indexBox build1Shu" @mousewheel="mouseWheel2">
          <div class="part2Index" ref="part2Index">
            <div
              class="Box"
              :style="{ transform: `translateY(${picTop}px)` }"
              ref="Box"
            >
              <div class="Box1">
                <div
                  class="imgBox1"
                  :style="{ transform: `translateY(${SrollTop1}px)` }"
                >
                  <img
                    src="@/assets/buildInfo/build2/米氏力奇-03.png"
                    alt=""
                    class="img1"
                  />
                </div>
                <div class="content1">
                  <p style="font-family: QingKai" class="content1P1">
                    Niki&小丸子
                  </p>
                  <p class="content1P2">
                    米氏力奇创始人——她们是一对同样喜欢烘培的闺蜜
                  </p>
                  <p class="content1P2">
                    为了把健康的面包分享出去的同时，也把健康的生活
                  </p>
                  <p class="content1P2">
                    生活理念分享给大家，米氏力奇便诞生了。
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="part3">
      <div
        class="part3 part part3Index"
        @mousewheel.prevent="part3Wheel"
        v-if="part3Show"
      >
        <div class="part3Box" ref="part3Box">
          <div class="part3Box1">
            <div
              class="part3ImgBox1"
              :style="{ transform: `translateY(${part3PicTop}px)` }"
            >
              <img
                src="@/assets/buildInfo/build2/米氏力奇-16.png"
                alt=""
                class="part3Img1"
              />
            </div>
            <div class="part3Content1">
              <p
                style="font-family: QingKai"
                class="part3Content1P1"
                :style="{ transform: `translateX(${part3SrollTop1}px)` }"
              >
                小巧精致的门面，外面放着两张亮黄色的高脚凳
              </p>
            </div>
            <div class="part3Content2">
              <p
                style="font-family: QingKai"
                class="part3Content2P2"
                :style="{ transform: `translateX(${part3SrollTop2}px)` }"
              >
                店内散发出柔黄的灯光，橱窗里的面包飘香诱人。
              </p>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="part4">
      <div
        class="part4 part part4Index"
        @mousewheel.prevent="part4Wheel"
        v-if="part4Show"
      >
        <div
          class="part4Box"
          :style="{ transform: `translateY(${part4PicTop}px)` }"
          ref="part4Box"
        >
          <div class="part4Box1">
            <div class="part4ImgBox1">
              <img
                src="@/assets/buildInfo/build2/米氏力奇-25.png"
                alt=""
                class="part4Img1"
              />
            </div>
            <div
              class="part4Content1"
              :style="{ transform: `translateY(${part4SrollTop1}px)` }"
            >
              <p style="font-family: QingKai" class="part4Content1P1">
                从侧面推开玻璃门，店内麻雀虽小却五脏俱全。墙上有个架子摆放着不同咖啡店的咖啡豆和小纸杯，主理人说这是因为他喜欢喝咖啡也喜欢收集这些有设计感的纸杯，所以就在架子上做起了“小展览”。
              </p>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="part5">
      <div class="part5 part" @mousewheel.prevent="part5Wheel" v-if="part5Show">
        <div
          class="banner banner5"
          ref="banner5"
          :style="{ left: imgLeft5 + 'px', zIndex: 10 }"
        >
          <img
            src="../../../assets/buildInfo/build2/米氏力奇-27.png"
            @load="imgLoad5"
            class="inlineImg"
            v-if="imgShow1"
            @mouseenter="imgEnter5(1)"
          />
          <img src="../../../assets/buildInfo/build2/米氏力奇-34.png" v-else />
          <img
            src="../../../assets/buildInfo/build2/米氏力奇-28.png"
            @load="imgLoad5"
            class="inlineImg"
            v-if="imgShow2"
            @mouseenter="imgEnter5(2)"
          />
          <img src="../../../assets/buildInfo/build2/米氏力奇-35.png" v-else />
          <img
            src="../../../assets/buildInfo/build2/米氏力奇-29.png"
            @load="imgLoad5"
            class="inlineImg"
            v-if="imgShow3"
            @mouseenter="imgEnter5(3)"
          />
          <img src="../../../assets/buildInfo/build2/米氏力奇-36.png" v-else />
          <img
            src="../../../assets/buildInfo/build2/米氏力奇-31.png"
            @load="imgLoad5"
            class="inlineImg"
            v-if="imgShow4"
            @mouseenter="imgEnter5(4)"
          />
          <img src="../../../assets/buildInfo/build2/米氏力奇-38.png" v-else />
          <img
            src="../../../assets/buildInfo/build2/米氏力奇-33.png"
            @load="imgLoad5"
            class="inlineImg"
            v-if="imgShow5"
            @mouseenter="imgEnter5(5)"
          />
          <img src="../../../assets/buildInfo/build2/米氏力奇-40.png" v-else />
        </div>
        <div
          class="word5 word"
          ref="word5"
          :style="{ top: wordTop5 + 'px', zIndex: 1 }"
        >
          <p>橱窗里的面包飘香诱人，</p>
          <p>以美式咖啡作为原料制成果冻……</p>
          <p>多款隐藏美食等着你来解锁哦！</p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BackHome from "../../../components/backHome.vue";
// import PoPup from "../../../components/popup.vue";

export default {
  name: "build25",
  data() {
    return {
      wordTop: 0,
      imgLeft: 0,
      imgWidth: null,
      wordHeight: null,
      screenY: null,
      screenX: null,
      imgLeft5: 0,
      imgWidth5: 0,
      wordTop5: 0,
      indexHeight: 0,
      part1Show: true,
      part2Show: false,
      part3Show: false,
      part4Show: false,
      part5Show: false,
      imgNum: 0,
      imgShow1: true,
      imgShow2: true,
      imgShow3: true,
      imgShow4: true,
      imgShow5: true,
      // 下面是part2内部的逻辑变量
      picTop: 0, //图片盒子的translateY
      BoxHeight: 0, //图片盒子元素的1/10高度
      srollY: 0,
      SrollTop1: 0,
      // 下面是part3内部的逻辑变量
      part3PicTop: 0, //图片盒子的translateY
      part3BoxHeight: 0, //图片盒子元素的4/7高度
      part3SrollTopY: 0,
      part3SrollTop1: 0,
      part3SrollTop2: 0,
      // 下面是part4内部的逻辑变量
      part4PicTop: 0, //图片盒子的translateY
      part4BoxHeight: 0, //图片盒子元素的1/2高度
      part4SrollY: 0,
      part4SrollTop1: 0
    };
  },
  components: {
    BackHome
    // PoPup,
  },
  mounted() {
    // this.buildIndex = this.$route.query.part2Index; //this.buildIndex是建筑的序号
    this.wordHeight = this.$refs.word1.offsetHeight;
    this.screenX = document.body.clientWidth;
    this.screenY = document.body.clientHeight;
    // this.indexHeight = this.$refs.part2Index.offsetHeight;
  },
  methods: {
    mouseWheel(e) {
      //deltaY-125往上滚
      //下面页面往上顶
      if (e.deltaY > 0) {
        this.wordTop =
          this.wordTop - 15 < -(this.wordHeight - this.screenY)
            ? -(this.wordHeight - this.screenY)
            : this.wordTop - 15;
        this.imgLeft =
          (this.wordTop * (this.imgWidth - this.screenX)) /
          (this.wordHeight - this.screenY);
      } else {
        //往上滚
        this.wordTop = this.wordTop + 15 > 0 ? 0 : this.wordTop + 15;
        this.imgLeft =
          (this.wordTop * (this.imgWidth - this.screenX)) /
          (this.wordHeight - this.screenY);
      }
      if (this.wordTop <= -(this.wordHeight - this.screenY)) {
        this.part2Show = true;
        setTimeout(() => {
          this.BoxHeight = (1 / 10) * this.$refs.Box.offsetHeight; //2/3元素的高度
        }, 0);
      } else {
        // 第二个页面消失
        this.part2Show = false;
      }
    },
    imgLoad() {
      this.imgWidth = this.$refs.banner1.offsetWidth;
    },
    mouseWheel2(e) {
      // 这里是part2内部逻辑
      this.srollY += e.wheelDeltaY * 0.15;
      if (this.srollY > 0) {
        this.srollY = 0;
        this.picTop = this.srollY;
      } else if (this.srollY > ~this.BoxHeight) {
        this.picTop = this.srollY;
      } else {
        this.srollY = ~this.BoxHeight;
        this.picTop = ~this.BoxHeight;
      }
      this.SrollTop1 = this.srollY * 0.2;
      // 下面是整个页面跳转逻辑
      if (this.part2Show) {
        this.indexHeight =
          this.indexHeight == 0
            ? this.$refs.part2Index.offsetHeight
            : this.indexHeight;
        if (this.$refs.part2Index.scrollTop == 0 && e.deltaY < 0) {
          //第二part消失
          this.part2Show = false;
          this.part3Show = false;
        }

        // if (
        //   this.$refs.part2Index.scrollTop >=
        //     this.screenY + this.indexHeight - 50 &&
        //   e.deltaY > 0
        // ) {
        //   //第三part出现
        //   this.part3Show = true;
        // }
        if (
          this.picTop <= -(this.indexHeight - this.screenY) + 10 &&
          e.deltaY > 0
        ) {
          //第三part出现
          this.part3Show = true;
          setTimeout(() => {
            this.part3BoxHeight = (4 / 7) * this.$refs.part3Box.offsetHeight; //4/7元素的高度
          }, 0);
        }
      }
    },
    part3Wheel(e) {
      // 这里是part3内部的逻辑
      this.part3SrollTopY += e.wheelDeltaY * 0.1;
      if (this.part3SrollTopY > 0) {
        this.part3SrollTopY = 0;
        this.part3PicTop = this.part3SrollTopY;
      } else if (this.part3SrollTopY > ~this.part3BoxHeight) {
        this.part3PicTop = this.part3SrollTopY;
      } else {
        this.part3SrollTopY = ~this.part3BoxHeight;
        this.part3PicTop = ~this.part3BoxHeight;
      }
      this.part3SrollTop1 = this.part3SrollTopY * 2.25;
      this.part3SrollTop1 = ~this.part3SrollTop1;
      this.part3SrollTop2 = this.part3SrollTopY * 2.25;

      // 下面是整体页面的跳转逻辑
      if (e.deltaY < 0) {
        this.part3Show = false;
      } else {
        this.part4Show = true;
        setTimeout(() => {
          this.part4BoxHeight = (1 / 2) * this.$refs.part4Box.offsetHeight; //1/2元素的高度
        }, 0);
      }
    },
    part4Wheel(e) {
      // 这里是part4页面内部逻辑
      this.part4SrollY += e.wheelDeltaY * 0.5;
      if (this.part4SrollY > 0) {
        this.part4SrollY = 0;
        this.part4PicTop = this.part4SrollY;
      } else if (this.part4SrollY > ~this.part4BoxHeight) {
        this.part4PicTop = this.part4SrollY;
      } else {
        this.part4SrollY = ~this.part4BoxHeight;
        this.part4PicTop = ~this.part4BoxHeight;
      }
      this.part4SrollTop1 = this.part4SrollY * 0.9;

      // 下面是总页面跳转逻辑
      if (e.deltaY < 0) {
        this.part4Show = false;
      } else {
        this.part5Show = true;
      }
    },
    part5Wheel(e) {
      if (this.part5Show) {
        let a = this.$refs.word5.offsetHeight;
        if (this.wordTop5 == 0 && e.deltaY < 0) {
          //回到前一页
          this.part5Show = false;
        }
        if (e.deltaY > 0) {
          this.wordTop5 =
            this.wordTop5 - 15 < -(a - this.screenY)
              ? -(a - this.screenY)
              : this.wordTop5 - 15;
          this.imgLeft5 =
            (this.wordTop5 * (this.imgWidth5 - this.screenX)) /
            (a - this.screenY);
        }
        if (e.deltaY <= 0) {
          this.wordTop5 = this.wordTop5 + 15 > 0 ? 0 : this.wordTop5 + 15;
          this.imgLeft5 =
            (this.wordTop5 * (this.imgWidth5 - this.screenX)) /
            (a - this.screenY);
        }
      }
    },
    imgLoad5() {
      this.imgNum++;
      if (this.imgNum >= 5) {
        this.imgWidth5 = this.$refs.banner5.offsetWidth;
      }
    },
    imgEnter5(num) {
      if (num == 1) {
        this.imgShow1 = false;
      } else if (num == 2) {
        this.imgShow2 = false;
      } else if (num == 3) {
        this.imgShow3 = false;
      } else if (num == 4) {
        this.imgShow4 = false;
      } else {
        this.imgShow5 = false;
      }
    },
    isDetail(is) {
      if (is) {
        //在这里要禁用鼠标不能滚动--背景板不能滚动
      } else {
        // 恢复鼠标滚轮事件
      }
    }
  }
};
</script>
<style scoped>
