  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  transition: 0.7s linear;
  background: blue;
}
.part1 {
  position: relative;
  background-color: rgb(225, 226, 209);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  transition: 0.7s linear;
}
.part2 {
  transition: 0.5s linear;
}
.part3 {
  background-color: rgb(229, 207, 188);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  transition: 1s linear;
}
.part5 {
  border: 0;
  background-color: rgb(226, 227, 209);
  color: rgb(116, 78, 50);
}
.part1-enter-active,
.part2-enter-active,
.part4-enter-active,
.part5-enter-active {
  transform: translate(0, 150vh) rotate(-10deg);
}
.part1-leave-active,
.part2-leave-active,
.part4-leave-active,
.part5-leave-active {
  transform: translate(0, 150vh) rotate(-10deg);
}
.part3-leave-active {
  opacity: 0;
  transform: translate(0, 150vh) rotate(-10deg);
}
.part3-enter-active {
  transition: cubic-bezier(0.01, 0.9, 0.1, 0.1);
  opacity: 0;
  transform: translate(0, 150vh) rotate(-10deg);
}
.banner {
  height: 100vh;
  padding: 0 10%;
  position: absolute;
  top: 0;
  transition: 1s;
}
.banner img {
  height: 100vh;
}
.word {
  transition: 1s;
  position: absolute;
  left: 0;
  font-size: 110px;
  width: 100vw;
  padding-top: 50px;
  padding-bottom: 35vh;
}
.word p {
  font-family: QingKai;
  text-align: center;
  margin-top: 30px;
  border: 0;
}
.pFirst {
  font-size: 150px;
}
.build1Shu {
  position: absolute;
  transition: 2s;
}
.indexBox {
  height: 100vh;
}
.part2Index {
  background-color: #e1e2d1;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.word5 {
  margin: 50vh 0;
  border: 0;
}

/* 下面是part2的class */
.part2Index {
  background-color: #e1e2d1;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.Box {
  width: 100%;
  height: 110vh;
  transition: 1s;
}
.Box1 {
  width: 100%;
  height: 100vh;
  position: relative;
}

.imgBox1 {
  width: 50vw;
  height: 110vh;
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 0;
  transition: 1s;
  display: flex;
  align-items: center;
}
.img1 {
  position: absolute;
  width: 90%;
}
.content1 {
  font-family: XiuKai;
  transition: 1s;
}
.content1 {
  position: absolute;
  bottom: 20vh;
  left: 10vw;
  text-align: left;
  z-index: 10;
}
.content1P1 {
  font-size: 5.6rem;
}
.content1P2 {
  font-family: FangSong;
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 5px;
}

/* 下面是part3的class */
.part3Index {
  background-color: #f7dbc4;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.part3Box {
  width: 100%;
  height: 110vh;
  transition: 1s;
}
.part3Box1 {
  width: 100%;
  height: 100vh;
  position: relative;
}
.part3ImgBox1 {
  width: 100vw;
  height: 100vh;
  position: absolute;
  z-index: 10;
  top: 60%;
  transition: 1s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.part3Img1 {
  position: absolute;
  width: 85%;
}

.wordBox {
  width: 100%;
  height: 300vh;
  transition: 1s;
}

.wordBox1 {
  width: 100%;
  height: 100vh;
  position: relative;
}

.part3Content1,
.part3Content2 {
  font-family: QingKai;
  transition: 1s;
  position: absolute;
  text-align: left;
}
.part3Content1 {
  height: 50%;
  z-index: 0;
  right: 0;
  display: flex;
  align-items: flex-end;
}
.part3Content2 {
  height: 50%;
  z-index: 20;
  bottom: 0%;
  display: flex;
  align-items: flex-start;
}
.part3Content1P1 {
  font-size: 7.8rem;
  white-space: nowrap;
  transition: 1s;
  color: #6f3c1d;
  text-align: left;
}
.part3Content2P2 {
  font-size: 7.8rem;
  white-space: nowrap;
  transition: 1s;
  color: #6f3c1d;
}
/* 下面是part4的class */
.part4Index {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.part4Box {
  width: 100%;
  height: 200vh;
  transition: 1s;
}
.part4Box1 {
  width: 100%;
  height: 200vh;
  position: relative;
}

.part4ImgBox1 {
  width: 100vw;
  height: 200vh;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  transition: 1s;
  display: flex;
  align-items: center;
}
.part4Img1 {
  position: absolute;
  width: 100%;
  height: 100%;
}
.word5 {
  padding: 35vh 0;
  border: 0;
}
.part4Content1 {
  width: 100%;
  position: absolute;
  top: 40vh;
  z-index: 10;
  transition: 1s;
  text-align: left;
}
.part4Content1P1 {
  text-indent: 2em;
  font-size: 10rem;
  color: white;
  letter-spacing: 0;
}
.banner5 {
  display: flex;
  margin: auto 0;
}
.inlineImg {
  display: inline;
}
.banner5 img {
  height: 50%;
  margin: auto 8vw;
}
</style>
